.product {
  display: flex;
  flex-direction: column;
  width: $widthCard;
  padding: 15px 14px;

  border: 1px solid #f3f3f3;
  background-color: $mainBackgroundColor;
  border-radius: 5px;

  &__img {
    align-self: center;
    box-sizing: content-box;
    padding: 12px 0;
    margin-bottom: 30px;

    border: 3px solid transparent;

    transition: transform 0.5s linear;
    cursor: pointer;

    &:hover {
      transform: scale(1.2);
      border-color: #f3f3f3;
    }
  }

  &__header {
    margin-bottom: 5px;

    @include mediumText;
  }

  &__code {
    margin-bottom: 15px;

    @include normalText;
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }

  &__reviews {
    display: flex;

    @include normalText;
    color: #000;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-bottom: 19px;

    @include normalText;
  }

  &__amount {
    @include boldText(16px);
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;

    @include boldText(14px);
    color: #fff;
    text-transform: uppercase;

    background-color: $blue;
    border-radius: 5px;
    border: 1px solid $blue;

    transition: background-color 0.5s, color 0.5s, border-color 0.5s;

    &:hover {
      background-color: $mainBackgroundColor;
      color: $blue;
    }
  }
}
